@import '../../../../styles/templarios.abstracts';

:host {
  --width: 100%;

  width: var(--width);
  overflow: hidden;
}

.images-carousel {
  &__container {
    border-radius: 8px;
    overflow: hidden;

    &::part(button-next),
    &::part(button-prev) {
      width: 48px;
      height: 48px;
      padding: 16px;
      box-sizing: border-box;
      border-radius: 50%;
      color: var(--tp-color-neutral-10) !important;
      background: rgba(var(--tp-color-neutral-2-rgb), 10%) !important;
      transition:
        background 300ms ease-in-out,
        transform 300ms ease-in-out;

      &:hover {
        background: rgba(var(--tp-color-neutral-2-rgb), 30%) !important;
      }

      &:active {
        transform: scale(0.75);
      }

      @include breakpoint-down('md') {
        width: 40px;
        height: 40px;
        padding: 12px;
      }
    }
  }

  &__slide {
    position: relative;
    height: 0;
    padding-top: calc(var(--aspect-ratio) * 100%);

    .swiper-zoom-container {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }

    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__thumbs {
    margin-top: 12px;
    visibility: hidden;
    position: absolute;
    left: -9999px;

    &--show {
      position: static;
      visibility: initial;
    }
  }

  &__thumb {
    display: flex;
    max-width: 120px;
    border-radius: 8px;
    cursor: pointer;
    overflow: hidden;
    filter: grayscale(100%);
    transition: 300ms filter ease-in-out;

    img {
      transition: 300ms transform ease-in-out;
    }

    &.swiper-slide-thumb-active {
      filter: grayscale(0%);
    }

    &:hover {
      img {
        transform: scale(1.1);
      }
    }
  }
}
