import { Component, Host, Prop, h } from '@stencil/core';
import { CarouselImage } from './utils/images-carousel.type';

@Component({
  tag: 'tp-images-carousel',
  styleUrl: 'images-carousel.scss',
  scoped: true,
})
export class TpImagesCarousel {
  /**
   * Os objetos com as imagens a serem mostradas no carrossel.
   */
  @Prop({ reflect: true }) images: CarouselImage[] = [];

  /**
   * As proporções do carrossel no formato h / w (height to width).
   */
  @Prop({ reflect: true }) aspectRatio: number = 1 / 1;

  /**
   * Se as images terão ou não o recurso de zoom.
   */
  @Prop({ reflect: true, mutable: true }) zoom: boolean = false;

  /**
   * Se o carrossel terá ou não o recurso de thumbs.
   */
  @Prop({ reflect: true }) thumbs: boolean = false;

  /**
   * Parâmetros de configuração do carrossel.
   */
  carouselParams = {
    slidesPerView: 1,
    navigation: true,
    loop: true,
  };

  /**
   * Parâmetros de configuração das thumbs.
   */
  thumbsParams = {
    spaceBetween: 12,
    watchSlidesProgress: true,
  };

  getCarouselImages() {
    const { images, zoom } = this;

    if (!images.length) {
      return (
        <swiper-slide>
          <ion-skeleton-text
            animated
            tp-component="tp-images-carousel"
          ></ion-skeleton-text>
        </swiper-slide>
      );
    }

    return zoom
      ? images.map(({ src, alt }) => (
          <swiper-slide class="images-carousel__slide">
            <div class="swiper-zoom-container">
              <img src={src} alt={alt} />
            </div>
          </swiper-slide>
        ))
      : images.map(({ src, alt }) => (
          <swiper-slide class="images-carousel__slide">
            <img src={src} alt={alt} />
          </swiper-slide>
        ));
  }

  getCarouselThumbs() {
    const { images, thumbs, thumbsParams } = this;

    return (
      <swiper-container
        class={{
          ['images-carousel__thumbs']: true,
          ['images-carousel__thumbs--show']: thumbs,
        }}
        slides-per-view={images.length}
        {...thumbsParams}
      >
        {images.map(({ src, alt }) => (
          <swiper-slide class="images-carousel__thumb">
            <img src={src} alt={alt} />
          </swiper-slide>
        ))}
      </swiper-container>
    );
  }

  render() {
    const { aspectRatio, zoom, carouselParams, thumbsParams } = this;

    const carouselImages = this.getCarouselImages();
    const carouselThumbs = this.getCarouselThumbs();
    const extraCarouselParams = {
      ['thumbs-swiper']: '.images-carousel__thumbs',
      zoom,
    };

    return (
      <Host
        class="images-carousel"
        style={{
          '--aspect-ratio': `${aspectRatio}`,
          '--thumbs-margin-top': `${thumbsParams.spaceBetween}`,
        }}
      >
        <swiper-container
          class="images-carousel__container"
          {...carouselParams}
          {...extraCarouselParams}
        >
          {carouselImages}
        </swiper-container>

        {carouselThumbs}
      </Host>
    );
  }
}
